.modal-professional-picture,
.modal-bodily-picture,
.modal-emotionally-picture,
.modal-social-picture {
  position: absolute;
  bottom: 0;
  right: 0;

  transform: translateX(150%);
  visibility: hidden;
  opacity: 0;
  transition: transform $default-transition-modal,
    right $default-transition-modal,
    opacity $default-transition-modal,
    visibility $default-transition-modal;
}

.active-picture {
  position: absolute;
  bottom: 0;
  right: 14.5%;
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
  transition: transform $default-transition-modal,
    right $default-transition-modal,
    opacity $default-transition-modal,
    visibility $default-transition-modal;

  @include viewport--lg {
    right: 10.5%;
  }
}

.benefit {
  position: relative;

  .modal-professional-list,
  .modal-bodily-list,
  .modal-emotionally-list,
  .modal-social-list {
    position: absolute;
    top: 138px;
    bottom: 0;
    right: 0;
    left: 0;
    transform: translateX(-150%);
    visibility: hidden;
    opacity: 0;
    transition: transform $default-transition-modal,
      right $default-transition-modal,
      opacity $default-transition-modal,
      visibility $default-transition-modal;

    @media (max-width: 926px) {
      top: 200px;
    }

    @media (max-width: 500px) {
      top: 250px;
    }

    @media (max-width: 408px) {
      top: 320px;
    }
  }

  .active-list {
    position: static;
    top: 140px;
    bottom: 0;
    right: 0;
    left: 0;
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
    transition: transform $default-transition-modal,
      right $default-transition-modal,
      opacity $default-transition-modal,
      visibility $default-transition-modal;

    @media (max-width: 926px) {
      top: 200px;
    }

    @media (max-width: 500px) {
      top: 250px;
    }

    @media (max-width: 408px) {
      top: 320px;
    }
  }
}

.text {
  position: relative;

  .modal-professional-title,
  .modal-bodily-title,
  .modal-emotionally-title,
  .modal-social-title {
    position: absolute;
    width: 38.473%;
    visibility: hidden;
    opacity: 0;
    transition: opacity $default-transition-settings,
      visibility $default-transition-settings;
  }

  .active-title {
    position: absolute;
    width: 38.473%;
    height: max-content;
    visibility: visible;
    opacity: 1;
    transition: opacity $default-transition-settings,
      visibility $default-transition-settings;
  }
}
