.header {
  position: relative;
  padding-top: 65px;
  min-height: 695px;
  min-width: 100%;
  max-width: 1440px;
  background-color: $color-default-white;
  overflow: hidden;

  @media (min-width: 1440px) {
    padding: 65px 0 0 0;
  }

  @include viewport--lg {
    padding-top: 15px;
  }

  @include viewport--sm {
    min-height: 540px;
  }

  @media (max-width: 500px) {
    min-height: 470px;
  }

  &--benefit {
    min-height: 518px;

    @include viewport--sm {
      min-height: 540px;
    }

    @media (max-width: 650px) {
      min-height: 470px;
    }
  }

  .header__img {
    position: absolute;
    bottom: 0;
    right: 0;

    width: 1002px;
    height: 678px;

    object-fit: cover;

    @include viewport--sm {
      width: 984px;
      height: 487px;
    }

    @media (max-width: 500px) {
      width: 284px;
      height: 287px;
    }
  }

  .header__img-benefit {
    max-width: 500px;
    max-height: 500px;

    img {
      object-fit: cover;
    }

    @media (max-width: 1000px) {
      max-width: 400px;
      max-height: 400px;
    }

    @media (max-width: 850px) {
      max-width: 300px;
      max-height: 300px;
    }

    @media (max-width: 650px) {
      max-width: 250px;
      max-height: 250px;
    }
  }

  .header__img-triangle {
    position: absolute;
    bottom: 0;
    right: 0;

    mix-blend-mode: multiply;
    z-index: 2;

    // свойство только для IE, mix-blend-mode IE не поддерживает
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
      opacity: 0.6;
    }
  }
}
