.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 209px;

  @include viewport--lg {
    padding: 0 9.069%;
  }

  @include viewport--md {
    padding: 0 7.684%;
  }

  @include viewport--sm {
    padding: 0 19px;
  }

  &--no-padding {
    @include viewport--sm {
      padding: 0;
    }
  }
}
