.text {
  position: relative;
  padding-left: 9.5%;
  z-index: 1;

  @include viewport--sm {
    padding-left: 5.5%;
  }

  &--benefit {
    padding-left: 14.5%;

    @include viewport--lg {
      padding-left: 9.5%;
    }

    @include viewport--sm {
      padding-left: 5.5%;
    }
  }
}

.text__meta {
  display: flex;
  flex-direction: row;
  margin-bottom: 71px;
  padding-left: 71px;

  @include viewport--sm {
    margin-bottom: 30px;
    padding-left: 10px;
  }

  &--benefit {
    padding-left: 0;
  }

  p {
    position: relative;
    width: 96px;
    margin: 0;
    padding: 0;

    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: $color-default-black;

    &::before {
      position: absolute;
      top: 2px;
      right: 0;
      content: "";
      width: 1.5px;
      height: 34px;
      background-color: $color-default-black;
    }
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    padding-left: 15px;

    li {
      font-weight: 700;
      font-size: 12px;
      line-height: 12px;
      letter-spacing: -1px;
      text-transform: uppercase;
      color: $color-default-black;
    }
  }
}

.text__title {
  max-width: 587px;
  width: 45%;
  min-height: 228px;
  margin-bottom: 32px;
  padding: 36px 0;
  padding-left: 4.5%;

  background-color: $color-nero;
  transform: rotate(-4.29deg);

  @include viewport--lg {
    padding-right: 4.5%;
  }

  @include viewport--sm {
    min-height: 127px;
  }

  @media (max-width: 500px) {
    padding-top: 20px;
    padding-bottom: 10px;
    margin-bottom: 20px;
    width: 180px;
  }

  svg {
    margin-bottom: 5px;
    transform: rotate(4.29deg);
    width: 89%;
  }

  h1,
  h2 {
    margin: 0;
    margin-left: 3px;
    padding: 0;
    font-weight: 700;
    font-size: calc(1rem + 0.75vw);
    line-height: 37px;
    color: $color-default-white;

    @media (min-width: 1439px) {
      font-size: 27px;
    }

    @include viewport--sm {
      font-size: calc(1rem + 0.05vw);
      line-height: 21px;
    }
  }
}

.text__title-benefit {
  h1,
  h2 {
    margin: 0;
    padding: 0;
    max-width: 524px;
    font-style: 700;
    font-size: 55px;
    line-height: 77px;

    @media (max-width: 1000px) {
      font-size: 41px;
      line-height: 65px;
    }

    @media (max-width: 570px) {
      font-size: 31px;
      line-height: 44px;
    }
  }
}

.text__slogan {
  padding-left: 70px;

  @include viewport--sm {
    padding-left: 10px;
  }

  p {
    margin: 0;
    padding: 0;

    font-weight: 700;
    font-size: 30px;
    line-height: 42px;
    color: $color-default-black;

    @media (max-width: 900px) {
      font-size: 24px;
      line-height: 32px;
    }

    @media (max-width: 500px) {
      font-size: 20px;
      line-height: 32px;
    }
  }
}
