@font-face {
  font-style: bold;
  font-weight: 700;
  font-family: "Tele2-DisplaySerif";

  font-display: swap;
  src:
    url("../fonts/tele2-displayserif-bold.woff2") format("woff2"),
    url("../fonts/tele2-displayserif-bold.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Tele2-DisplaySerif";

  font-display: swap;
  src:
    url("../fonts/tele2-displayserif-regular.woff2") format("woff2"),
    url("../fonts/tele2-displayserif-regular.woff") format("woff");
}
