.benefit {
  margin-top: 40px;
  margin-bottom: 200px;

  @include viewport--sm {
    margin-top: 0;
  }
}

.benefit__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.benefit__list-item {
  width: 100%;
  margin-bottom: 40px;
  padding: 31px 3.719% 38px 5.774%;
  border: 1.625px solid $color-default-black;
  box-sizing: border-box;
  border-radius: 7.3125px;

  @include viewport--sm {
    padding: 15px;
  }
}

.benefit__flex-container-row {
  display: flex;
  flex-direction: row;

  @include viewport--sm {
    flex-direction: column;
  }
}

.benefit__flex-container-column {
  display: flex;
  flex-direction: column;
}

.benefit__text-container {
  width: 49.73%;

  @include viewport--sm {
    width: 100%;
    text-align: center;
  }
}

.benefit__icon {
  min-width: 133px;
  height: 133px;
  margin-right: 5.96%;

  @include viewport--sm {
    width: 100px;
    margin: 0 auto 10px;
  }
}

.benefit__list-item h3 {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;

  font-weight: 700;
  font-size: 31px;
  line-height: 44px;
  color: $color-nero;

  @include viewport--sm {
    font-size: 27px;
    line-height: 40px;
  }
}

.benefit__list-item p {
  max-width: 433px;
  margin: 0;
  font-size: 19px;
  line-height: 27px;
  color: $color-nero-2;
  text-align: left;

  @include viewport--md {
    font-size: 18px;
    line-height: 26px;
    max-width: 100%;
  }
}

.benefit__col-last {
  position: relative;
  padding: 10px 10px;
  width: 22.76%;
  min-height: 142px;
  margin-left: auto;

  @include viewport--sm {
    min-width: 180px;
  }

  .benefit__flex-container-row {
    justify-content: space-between;
  }

  .benefit__button {
    position: absolute;
    bottom: 0;
    right: 0;
    display: block;
    width: 66px;
    height: 66px;
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: 0;
    border-radius: 50%;
    outline: none;
    cursor: pointer;

    & svg {
      width: 66px;
      height: 66px;
      fill: $color-inch-worm;
      transform: rotate(90deg);
      transition: transform $default-transition-settings;
    }
  }

  @include viewport--sm {
    width: 100%;
    min-height: auto;
    margin-left: 0;
    padding: 0;
  }
}

.benefit__button:hover svg,
.benefit__button:focus svg {
  fill: $color-nero;
  transition: fill $default-transition-settings;
}

.benefit__button:active svg {
  fill: rgba($color-nero, 0.7);
}

.benefit__button.benefit__button--arrow-rotate svg {
  fill: $color-nero;
  transform: rotate(270deg);
  transition: transform $default-transition-settings;
}

.benefit__button.benefit__button--arrow-rotate:hover svg,
.benefit__button.benefit__button--arrow-rotate:focus svg {
  opacity: 0.7;
}

.benefit__span-bonus {
  margin-right: 15px;
  padding: 2px 7px 5px;

  font-weight: bold;
  font-size: 15px;
  line-height: 21px;
  color: $color-nero;

  background: $color-dark-tangerine;
  border-radius: 4px;

  @include viewport--md {
    font-size: 13px;
    line-height: 18px;
  }

  @include viewport--sm {
    margin-top: 15px;
    margin-right: 0;
    text-align: center;
  }
}

.benefit__span-number {
  margin-left: auto;
  min-width: 40px;
  max-height: 30px;
  padding: 0 7px 1px;
  font-size: 19px;
  line-height: 27px;
  color: $color-grey;
  background: transparent;
  text-align: center;
  border: 1px solid $color-very-light-grey;
  border-radius: 8px;

  @include viewport--md {
    font-size: 16px;
    line-height: 23px;
  }

  @include viewport--sm {
    max-width: 26px;
    margin-top: 60px;
    margin-left: 0;
  }
}
