.accordion {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.accordion__item {
  position: absolute;
  width: 100%;
  min-height: 141px;
  margin-bottom: 12px;
  padding: 16px 3.621% 18px 3.132%;
  background: $color-snow;
  border: 1px solid $color-whisper;
  border-radius: 9px;
  visibility: hidden;
  opacity: 0;

  &--shown {
    position: static;
    display: block;
    height: max-content;
    visibility: visible;
    opacity: 1;
    transition: opacity 0.3s ease-out;
  }
}

.accordion__item:nth-child(2) {
  margin-top: -25px;
}

div.accordion__item:nth-child(5) {
  margin-bottom: 86px;
}

.accordion__flex-container {
  display: flex;
  align-items: center;

  @include viewport--sm {
    flex-direction: column;
  }
}

.accordion__type-container {
  display: flex;
  align-self: flex-start;
  width: 175px;
  margin-top: 1px;

  @include viewport--sm {
    align-self: center;
  }
}

.accordion__text-container {
  align-self: flex-start;
  width: 51.315%;
  margin-left: 4.207%;
  margin-right: auto;

  @include viewport--md {
    width: 40.556%;
  }

  @include viewport--sm {
    width: 100%;
    margin: 0;
  }
}

.accordion__item span {
  padding: 1px 10px 6px 9px;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $color-zambezi;
  border: 1px solid $color-inch-worm;
  border-radius: 14px;

  @include viewport--md {
    font-size: 14px;
    line-height: 18px;
  }

  @include viewport--sm {
    margin: 0 auto;
  }
}

.accordion__type-container--orange span {
  border-color: $color-dark-tangerine;
}

.accordion__item h4 {
  margin: 0;
  margin-bottom: 9px;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 23px;
  line-height: 32px;
  color: $color-night-rider;

  @include viewport--sm {
    margin-top: 9px;
    text-align: center;
  }
}

.accordion__item p {
  margin: 0;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $color-zambezi;

  @include viewport--sm {
    text-align: center;
  }
}

.accordion__link {
  width: 18.823%;
  padding: 7px 15px 10px;
  font-family: $font-family--tele2-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: $color-default-black;
  background: $color-inch-worm;
  border-radius: 40px;

  &--orange {
    background: $color-dark-tangerine;
  }

  &:hover,
  &:focus {
    background: transparent;
    border: 1px solid $color-inch-worm;
    transition: background $default-transition-settings;
    outline: none;
  }

  &:active {
    opacity: 0.5;
    transition: opacity $default-transition-settings;
  }

  @include viewport--md {
    padding: 7px 10px 10px;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }

  @include viewport--sm {
    margin-top: 25px;
    width: 100px;
  }
}

.accordion__item--bonus .accordion__link--orange:hover,
.accordion__item--bonus .accordion__link--orange:focus {
  background: transparent;
  border: 1px solid $color-dark-tangerine;
  transition: background $default-transition-settings;
}

.accordion__qr-link {
  margin-right: 26px;

  &:hover,
  &:focus {
    outline: 2px solid $color-inch-worm;
    outline-offset: 3px;
    transition: outline-offset $default-transition-settings;
  }

  @include viewport--sm {
    margin-top: 25px;
    margin-right: 0;
  }
}
