//- Кнопки
.benefit__list {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin: 0;
  margin-bottom: 62px;
  padding: 0;
  list-style: none;

  @include viewport--sm {
    margin-bottom: 25px;
  }

  .benefit-button__item {
    @include viewport--sm {
      max-height: 70px;
      height: 100%;
    }
  }

  input[type="radio"] {
    visibility: hidden;
    height: 0;
    width: 0;
  }

  label {
    display: flex;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    text-align: center;

    padding: 5px 29px 10px;
    max-width: 258px;
    min-height: 47px;

    font-size: 22px;
    line-height: 31px;
    text-transform: lowercase;
    color: $color-eclipse;
    border: 3px solid $color-inch-worm;
    border-radius: 67px;

    transition: color $default-transition-settings, background-color $default-transition-settings;
    user-select: none;
    cursor: pointer;

    @include viewport--md {
      margin-right: 10px;
      font-size: 18px;
      line-height: 20px;
    }

    @include viewport--sm {
      max-width: 200px;
    }
  }

  input[type="radio"]:checked + label {
    background-color: $color-inch-worm;
    color: $color-default-black;
    padding: 5px 29px 10px;
  }

  input[type="radio"]:hover:not(:checked) + label {
    background-color: rgba(195, 228, 0, 0.3);
    border: 3px solid $color-inch-worm;
    border-radius: 67px;
    color: $color-eclipse;
  }
}

.benefit__item {
  padding-right: 20px;

  &:last-child {
    padding-right: 0;
  }
}
