.nav {
  margin-left: -8px;
  margin-bottom: 15px;
  max-width: 150px;
}

.nav__link {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-size: 22px;
  line-height: 31px;
  outline: 0;

  &:hover,
  &:focus {
    p {
      color: $color-default-black;
    }

    svg {
      fill: $color-default-black;
    }
  }

  &:active {
    p {
      color: $color-default-black;
      opacity: 0.5;
    }

    svg {
      fill: $color-default-black;
      opacity: 0.5;
    }
  }

  svg {
    margin-right: 23px;
    fill: $color-inch-worm;
    transition: fill $default-transition-settings;
  }

  p {
    margin: 0;
    padding: 0;
    transition: color $default-transition-settings;
    color: $color-suva-grey-2;
  }

  @media (max-width: 500px) {
    font-size: 18px;
  }
}
